.row .col[class*=pull-],
.row .col[class*=push-] {
  position: relative;
}

.row:after {
  clear: both;
  content: "";
  display: table;
}

.container {
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 16px;
  width: 100%;
}

.row .col.s1,
.row .col.s10,
.row .col.s11,
.row .col.s12,
.row .col.s2,
.row .col.s3,
.row .col.s4,
.row .col.s5,
.row .col.s7,
.row .col.s8,
.row .col.s9 {
  left: auto;
  margin-left: auto;
  right: auto;
}

.row {
  margin: 0 -5px;
}

.row .col {
  float: left;
  padding: 0 5px;
}

.row .col.s1 {
  width: 8%;
}

.row .col.s2 {
  width: 16%;
}

.row .col.s3 {
  width: 25%;
}

.row .col.s4 {
  width: 33.333%;
}

.row .col.s5 {
  width: 41%;
}

.row .col.s6 {
  left: auto;
  margin-left: auto;
  right: auto;
  width: 50%;
}

.row .col.s7 {
  width: 58%;
}

.row .col.s8 {
  width: 66%;
}

.row .col.s9 {
  width: 75%;
}

.row .col.s10 {
  width: 83%;
}

.row .col.s11 {
  width: 91%;
}

.row .col.s12 {
  width: 100%;
}

