.links {
    background-color: #1a1a1a;
    border-radius: 40px;
    display: block;
    justify-content: center;
    padding: 16px 0;
    position: relative;
    top: 32px;

    @media (prefers-color-scheme: dark) {
        background-color: #000;
    }

    @media (--tablet) {
        display: flex;
    }

    & > a {
        color: #fff;
        border-right: 0;
        display: block;
        font-family: var(--font-header);
        font-size: 16px;
        font-weight: 400;
        padding: 8px 8px;
        text-align: center;
        text-decoration: none;

        @media (prefers-color-scheme: dark) {
            color: #ddd;
        }

        &:last-child {
            border-right: 0;
        }

        @media (--tablet) {
            border-right: 1px solid #666;
            font-size: 16px;
            padding: 8px 16px;
        }

        @media (--desktop) {
            font-size: 20px;
        }

        &:hover {
            color: var(--color-yellow);
        }
    }
}
